import React from 'react'
import TplGlobal from './template-global'
import BlockInfo from '../blocks/blockInfo'
import BlockProject from '../blocks/blockProject'
import Interests from '../blocks/interests'
import Input from '../form/input'
import Button from '../form/button'
import {mockProjects, } from '../mocks/mocks'
import * as users from '../mocks/data/users'
import BlockUsers from '../blocks/users'
import BlockInterests from '../blocks/interests'

const TplList = (props) => {
    const {title, children} = props;

    const contributors = [
        {
            user: users.u8,
            interests: [3,4,6,7,9] 
        },
        {
            user: users.u2,
            interests: [7,8,9,10,15] 
        },
        {
            user: users.u14,
            interests: [5,7,1,9,10,12,13] 
        },
        {
            user: users.u21,
            interests: [1,2,3,4,5,6,7] 
        },
    ]

    return (
    

        <TplGlobal title={title}>
            <div className="main">
               <div className="sections">

                    <div className="section list">
                        
                        {children}

                    </div>
                    <div className="section blocks">
                        
                        <BlockInfo title="Top Contributors">
                            {
                                contributors.map((item, idx)=> {
                                    return (
                                        
                                        <div className="contributor">
                                            <BlockUsers users={[item.user]} name />
                                            <BlockInterests interests={item.interests} />

                                        </div>
                                    )
                                })
                            }
                        </BlockInfo>
                        
                        <BlockInfo title="Featured" last>
                        CONTENT
                        </BlockInfo>

                    </div>



               </div>
               
            </div>

                <style jsx>
                {`  
                   .contributor {
                       margin-top: 10px;
                   }
                   .main {
                     padding-left: var(--main-margin);
                    }

                    .sections {
                        display: flex;
                        flex-direction: row;


                    }

                    .section {
                        width: 260px;
                    }
                   
                    .list {
                        display: flex;
                        flex-grow: 2;
                        margin-right: 25px;
                        flex-direction: column;

                    }

                    .blocks {
                    }

                   
                  
                `}

                </style>
        </TplGlobal>

        
    )
}

export default TplList;